* {
  margin: 0;
  padding: 0;
  border: 0;
}

body {
  background-color: rgb(80, 74, 74);
}
.App {
  text-align: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  font-size: large;
}

form {
  width: 300px;
  padding: 1rem;
  padding-bottom: 3rem;
  margin: auto;
  background-color: rgb(211, 196, 174);
  border: 0.4em outset rgb(131, 54, 35);
  border-radius: 1em;
  position: relative;
}

#results-section {
  width: 300px;
  padding: 1rem;
  padding-top: 0;
  padding-bottom: 3rem;
  margin: auto;
  background-color: rgb(211, 196, 174);
  border: 0.4em outset rgb(131, 54, 35);
  border-radius: 1em;
  border-top: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  position: relative;
  top: -2em;
}

select {
  margin: 1em;
  margin-bottom: 3em;
  padding: 0.3em;
  padding-left: 0.7em;
  font-size: large;
  border: 0.2em inset darkgray;
  border-radius: 1em;
  background-color: rgb(216, 212, 205);
  color: rgb(37, 37, 37);
  display: inline-block;
}

button {
  /* margin-top: 2rem; */
  border: 0.25em outset rgb(128, 118, 107);
  border-radius: 1em;
  font-size: large;
  padding: 0.5em;
  background-color: rgb(179, 167, 157);
  font-weight: bolder;
  color: rgb(15, 15, 15);
}

h1 {
  background-color: black;
  color: bisque;
  margin-bottom: 5rem;
  padding: 0.5em;
}

h2 {
  margin: 0;
  margin-top: 1em;
}
section {
  display: inline-block;
}

label {
  font-weight: bolder;
}

#number-of-sides {
  margin-left: 0.3em;
}

#number-of-dice {
  margin-right: 0.27em;
}

#roll-history {
  width: 250px;
  height: 25px;
  padding: 0;
  margin: auto;
  background-color: rgb(136, 106, 89);
  border: 0.2em outset rgb(131, 54, 35);
  border-radius: 1em;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  position: relative;
  top: 9em;
  right: -10.3em;
  transform: rotate(90deg);
}

#history-tab-text {
  font-size: small;
  margin: 0;
  padding: 88px;
  padding-bottom: 10px;
  padding-top: 5px;
  position: relative;
  top: 1px;
}

#full-history {
  width: 200px;
  height: 470px;
  padding: 0;
  margin: auto;
  background-color: rgb(136, 106, 89);
  border: 0.2em outset rgb(131, 54, 35);
  border-radius: 1em;
  position: relative;
  top: -19.2em;
  left: 5.1em;
  transform: rotate(-90deg);
}

#full-history-title {
  background-color: rgb(160, 143, 125);
  font-size: small;
  margin-top: 1em;
  margin-bottom: 0.5em;
  padding: 0.2em;
  border: 0.1em solid rgb(63, 36, 29);
  border-right: none;
  border-left: none;
}

#full-history-text {
  font-size: 0.7em;
  margin: 0;
}

.roll-total-text {
  font-weight: bold;
}

li {
  padding: 0;
}

.history-separator {
  border: 0.1em solid rgb(63, 36, 29);
  margin: 5px;
  width: 99%;
  position: relative;
  right: 5px;
}

.full-history-button {
  font-size: small;
  margin-left: 10px;
  margin-right: 15px;
}

#hide-result {
  margin-top: 1em;
}

ol {
  list-style-type: none;
}
